.main__content_body {
  padding: 100px 0;
}

.main__content_title {
  font-family: cinzel_regular;
  color: #e1ad4f;
  margin-top: 20px;
  margin-bottom: 35px;
  text-transform: uppercase;
}

.main__content_desc {
  span {
    font-family: FiraSans-Bold;
    color: #493e3e;
    font-size: 20px;
    font-weight: 700;
    line-height: 29px;
    font-style: normal;
  }

  a {
    color: #e1ad4f;
  }

  a:hover {
    color: #d89825;
  }
}

.pb-60 {
  padding-bottom: 60px;
}

.main__content_services_title {
  font-family: FiraSans-Bold !important;
}

.main__content_services {
  border-bottom: 1px solid #eee;
  padding: 5px 0 5px 0;
}

.main__content_services_about h5 {
  font-size: 14px;
  font-family: Open Sans, sans-serif;
  font-weight: 600;
  margin-top: 10px;
  margin-bottom: 10px;
}

.main__content_service {
  margin-top: 80px;
}

@media only screen and (max-width: 768px) {
  .fs-sm-14 {
    font-size: 14px !important;
  }
  .fs-sm-22 {
    font-size: 22px !important;
  }

  .main__content_services {
    border-bottom: 1px solid #eee;
    padding: 5px 0 5px 0;
    margin-bottom: 20px;
  }
  .w-mb-100{
    width: 100%!important;
  }

}
