.main_form{
  background-image: url(/img/contact-bg.webp);
  padding: 90px 0 100px 0;
  background-repeat: no-repeat;
  background-size: cover;
}
.main_form_title {
  font-family: cinzel_regular;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: black !important;
}

.main_form_form {
  input {
    background: rgba(0, 0, 0, 0.4);
    margin-bottom: 20px;
  }
  textarea{
    background: rgba(0, 0, 0, 0.4);
  }
  .btn-success{
    background: #e1ad4f;
    border-color: #e1ad4f;
    color: #fff;
    font-size: 14px;
    padding: 10px 30px;
    text-transform: uppercase;
    border-radius: 3px;
  }
  .btn-success:hover{
    background: #e1ad4f!important;
    border-color: #e1ad4f !important;
  }
}