body {
  font-family: FiraSans-Light;
  font-size: 16px;
  font-weight: 300;
  background-color: white;
}
img{
  max-width: 100%;
}
.header{
  height: 70px;
  width: 100%;
  box-shadow: 0 3px 3px rgb(0 0 0 / 5%);
  opacity: .95;
  z-index: 9999;
  background-color: #fff;
  position: relative;
}
.footer{
  background-color: #493e3e;
  padding: 35px 0 0 0;
  .copyright{
    font-size: 14px;
    color: #fff;
  }
}
.sb{
  background-image: url(/img/sb.webp);
  width: 32px;
  height: 32px;
  background-size: auto 32px;
  display: inline-block;
}
.menu-fixed {
  background-color: #fff !important;
  position: fixed !important;
  top: 0 !important;
  height: 60px;
}
.menu-fixed .header__navbar_logo {
  width: 48px!important;
  -webkit-transition: 300ms;
  -o-transition: 300ms;
  transition: 300ms;
}
.menu-fixed  >li {
  height: 60px;
}
.menu-fixed.has-slideshow >li >a,
.menu-fixed >li >a {
  line-height: 60px;
}
.menu-fixedul.menu li a {
  margin-top: 15px;
}
a{
  color: #e1ad4f;
}