.main__content_news {
  h3 {
    font-family: cinzel_regular;
    font-size: 36px;
    line-height: 36px;
    font-weight: 700;
    margin-bottom: 100px;
  }
}

.block_news {
  width: 410px !important;
  height: 390px !important;

  img {
    height: 390px !important;
  }
}

.block_news_text {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 200px 0 0 10px;
  border-radius: 0;

  a {
    font-size: 24px;
    font-family: Fira Sans, sans-serif;
    color: #fff;
    text-decoration: none;
    margin: 10px 0 0 0;
  }

  a:hover {
    color: #fff;
  }

  p {
    margin: 10px 0 10px 0;;
    font-family: Fira Sans, sans-serif;
    color: #fff;
  }
}

.block_news_text:hover {
  background-color: rgba(225, 173, 79, 0.9);
  transition: 300ms;
}
